body {
    background-color: #f5f5f5
}

.navbar {
    height: 90px;
    background-color: #fff;
    line-height: 90px;
    box-shadow: 1px 1px 10px #888
}

.nav-logo images {
    width: 241px;
    height: 72px
}

.navbar a {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    text-decoration: none;
    display: inline-block;
    border-radius: 10px;
    padding: 10px 15px;
    margin-right: 5px;
    font-family: Adobe Heiti Std R;
    color: #6b7a82
}

.navbar a:last-child {
    font-size: 20px;
    line-height: 22px;
    text-decoration: none;
    display: inline-block;
    border-radius: 10px;
    padding: 10px 15px;
    margin-right: 5px;
    font-family: Adobe Heiti Std R;
    color: rgba(255, 254, 254, 1);
    background: #fee56f
}

.navbar a:hover {
    font-size: 20px;
    line-height: 22px;
    text-decoration: none;
    display: inline-block;
    border-radius: 10px;
    padding: 10px 15px;
    margin-right: 5px;
    font-family: Adobe Heiti Std R;
    color: rgba(255, 254, 254, 1);
    background: #fee56f
}

.content {
    height: 840px
}

.content-wrapper {
    background-color: #fff;
    height: 840px
}

.aside ul {
    height: 840px
}

.aside li {
    width: 300px;
    height: 7.7%;
    line-height: 64px;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
    -webkit-justify-content: center;
    justify-content: center
}

.aside-content-active {
    width: 270px !important;
    height: 38px !important;
    line-height: 38px !important;
    color: #fff;
    background: url(../images/img_1.png) no-repeat
}

li:active .aside-content {
    width: 270px !important;
    height: 38px !important;
    line-height: 38px !important;
    color: #fff;
    background: url(../images/img_1.png) no-repeat
}

.aside-content-hover {
    width: 270px;
    height: 38px;
    line-height: 38px;
    color: #fff;
    background: url(../images/img_2.png) no-repeat
}

.icon {
    margin-top: 14px;
    width: 18px;
    height: 100%;
    background: url(../images/arrow-icon.png) no-repeat;
    float: left
}

.article {
    height: 100%;
    margin-left: 318px
}

.page {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    height: 100%;
    display: none;
    padding-left: 44px
}

.page-title {
    font-family: NotoSansHans-Regular;
    color: #fee56f;
    font-size: 18px;
    margin-top: 46px
}

.page ul {
    font-family: NotoSansHans-Regular;
    color: #000;
    margin-top: 21px
}

.page ul li {
    padding: 6px
}

.page-footer {
    margin-top: 20px;
    font-family: Microsoft YaHei;
    color: #6b6b6b;
    position: absolute;
    width: 100%;
    height: 104px;
    background: #e6e6e6
}

.page-footer-content {
    font-size: 18px;
    line-height: 18px;
    margin: 5px auto;
    font-size: 14px;
    text-align: center;
    padding: 5px;
    line-height: 25px
}

.page-footer-content span {
    margin-right: 30px
}

.contact span {
    margin-right: 20px
}

@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
    .page-footer {
        margin-top: 20px;
        font-family: Microsoft YaHei;
        color: #6b6b6b;
        position: absolute;
        width: 100%;
        height: 100px;
        background: #e6e6e6
    }

    .page-footer-content {
        font-size: 18px;
        line-height: 18px;
        margin: 5px auto;
        font-size: 14px;
        text-align: center;
        padding: 5px;
        line-height: 25px
    }

    .page-footer-content span {
        margin-right: 20px
    }

    .contact span {
        margin-right: 40px
    }
}

.nav-haeder {
    font-family: NotoSansHans-Medium;
    font-size: 15px;
    font-weight: 700;
    text-align: center;
    height: 50px;
    line-height: 50px;
    color: #000;
    background: rgba(255, 255, 255, 1)
}

.nav-haeder-goback {
    position: relative;
    top: 4px;
    right: 36%;
    width: 11px;
    height: 20px;
    display: inline-block;
   // background: url(../images/goback.png) no-repeat;
    background-size: 100% 100%
}

.p-content {
    padding: 10px
}

.p-title {
    color: #4d4c4c;
    margin-top: 16px;
    margin-right: 24px;
    margin-bottom: 10px
}

.p-article li {
    font-family: NotoSansHans-Regular;
    float: left;
    background-color: #73c2ff;
    padding: 5px;
    margin-right: 15px;
    margin-top: 10px;
    border-radius: 5px;
    color: #fff
}

.p-more {
    margin-top: 25px;
    text-align: center;
    color: #717171
}

.p-article-images {
    margin-top: 40px;
    margin-left: 20px;
    vertical-align: middle
}

.p-article-images images {
    width: 268px;
    height: 287px
}